import React, { PureComponent } from 'react'

import PropTypes from 'prop-types'

import './index.less'

export default class Radio extends PureComponent {
  static propTypes = {
    className: PropTypes.string,
    style: PropTypes.object,
    iconClassName: PropTypes.string,
    iconStyle: PropTypes.object,
    labelClassName: PropTypes.string,
    labelStyle: PropTypes.object,
    defaultChecked: PropTypes.bool,
    checked: PropTypes.bool,
    label: PropTypes.any,
    trigger: PropTypes.oneOf(['icon', 'label', 'all']),
    onChange: PropTypes.func
  }

  static defaultProps = {
    trigger: 'all'
  }

  constructor (props) {
    super(props)
    this.state = {
      checked: props.checked || props.defaultChecked
    }
  }

  static getDerivedStateFromProps (props, state) {
    if (('checked' in props) && props.checked !== state.checked) {
      return { checked: props.checked }
    }
    return null
  }

  onClick = () => () => {
    const { onChange } = this.props
    const checked = !this.state.checked
    onChange && onChange(checked)
    this.setState({
      checked: checked
    })
  }

  render () {
    const {
      className, style,
      iconClassName, iconStyle,
      labelClassName, labelStyle,
      label, trigger
    } = this.props
    const { checked } = this.state
    const iconClickProps = trigger === 'icon' || trigger === 'all' ? { onClick: this.onClick() } : {}
    const labelClickProps = trigger === 'label' || trigger === 'all' ? { onClick: this.onClick() } : {}
    return (
      <div
        style={style}
        className={`radio ${className}`}>
        <svg
          className={`radio-icon ${iconClassName} ${checked ? 'radio-icon-checked' : ''}`}
          style={iconStyle}
          {...iconClickProps}
          viewBox="0 0 1024 1024">
          <path
            d="M512 85.333333C276.48 85.333333 85.333333 276.48 85.333333 512S276.48 938.666667 512 938.666667 938.666667 747.52 938.666667 512 747.52 85.333333 512 85.333333z m0 796.444445c-203.662222 0-369.777778-166.115556-369.777778-369.777778S308.337778 142.222222 512 142.222222 881.777778 308.337778 881.777778 512 715.662222 881.777778 512 881.777778z"/>
          {
            checked &&
            <path
              d="M512 512m-250.311111 0a250.311111 250.311111 0 1 0 500.622222 0 250.311111 250.311111 0 1 0-500.622222 0Z"/>
          }
        </svg>
        {
          label &&
          <span
            style={labelStyle}
            {...labelClickProps}
            className={`radio-label ${labelClassName} ${checked ? 'radio-label-checked' : ''}`}>
            {label}
          </span>
        }
      </div>
    )
  }
}
