import React, { PureComponent } from 'react'

import PropTypes from 'prop-types'

import { SketchPicker } from 'react-color'

import Button from '../button'

import './index.less'

export default class ColorPicker extends PureComponent {
  static propTypes = {
    className: PropTypes.string,
    style: PropTypes.object,
    labelClassName: PropTypes.string,
    labelStyle: PropTypes.object,
    defaultColor: PropTypes.string,
    color: PropTypes.string,
    label: PropTypes.any,
    onChange: PropTypes.func
  }

  constructor (props) {
    super(props)
    this.state = {
      showPicker: false,
      color: props.color || props.defaultColor
    }
  }

  static getDerivedStateFromProps (props, state) {
    if (('color' in props) && props.color !== state.color) {
      return { color: props.color }
    }
    return null
  }

  onChangeComplete = (color) => {
    const { onChange } = this.props
    onChange && onChange(color.hex)
    this.setState({
      color: color.hex
    })
  }

  render () {
    const { className, style, labelClassName, labelStyle, label } = this.props
    const { showPicker, color } = this.state
    return (
      <div
        style={style}
        tabIndex="0"
        onFocus={() => {
          if (!showPicker) {
            this.setState({
              showPicker: true
            })
          }
        }}
        onBlur={() => {
          if (showPicker) {
            this.setState({
              showPicker: false
            })
          }
        }}
        className={`color-picker ${className}`}>
        <div
          className="color-picker-container">
          <Button
            style={{ background: color }}
            className="color-picker-color-box"/>
          <div
            className={`color-picker-content-container ${showPicker ? 'color-picker-content-container-show' : ''}`}>
            <i
              className="color-picker-content-arrow"/>
            <SketchPicker
              disableAlpha
              color={color}
              onChangeComplete={ this.onChangeComplete }/>
          </div>
        </div>
        {
          label &&
          <span
            style={labelStyle}
            className={`color-picker-label ${labelClassName}`}>
            {label}
          </span>
        }
      </div>
    )
  }
}
