import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'

import intl from '../../../component/intl'

import './top-bar.less'

export default class TopBar extends PureComponent {
  static propTypes = {
    theme: PropTypes.string,
    language: PropTypes.string,
    period: PropTypes.string,
    onTechnicalIndicatorClick: PropTypes.func,
    onSettingClick: PropTypes.func,
    onPeriodClick: PropTypes.func,
    onLanguageClick: PropTypes.func,
    onThemeClick: PropTypes.func,
    onScreenshotClick: PropTypes.func
  }

  onPeriodChange = (period) => () => {
    const { onPeriodClick } = this.props
    onPeriodClick && onPeriodClick(period)
  }

  render () {
    const {
      theme, language, period,
      onTechnicalIndicatorClick, onSettingClick, onLanguageClick,
      onThemeClick, onScreenshotClick
    } = this.props
    return (
      <div
        className={`top-bar-${theme}`}>
        <div
          className="top-bar-periods-small">
          {
            ['Line', '1m', '15m', '1H', '4H', '1D', '1W'].map(p => {
              return (
                <span
                  key={p}
                  className={`top-bar-item ${p === period && 'top-bar-item-selected'}`}
                  onClick={this.onPeriodChange(p)}>
                  {p}
                </span>
              )
            })
          }
        </div>
        <div
          className="top-bar-periods">
          {
            ['Line', '1m', '5m', '15m', '30m', '1H', '2H', '4H', '6H', '12H', '1D', '1W'].map(p => {
              return (
                <span
                  key={p}
                  className={`top-bar-item ${p === period && 'top-bar-item-selected'}`}
                  onClick={this.onPeriodChange(p)}>
                  {p}
                </span>
              )
            })
          }
        </div>
        <div
          className="top-bar-tools">
          <i className="top-bar-split-line"/>
          <span
            className="top-bar-icon-item top-bar-item"
            onClick={onTechnicalIndicatorClick}>
            <i
              className="top-bar-icon icon-technical-indicator"/>
            <span>{intl.get('indicator', language)}</span>
          </span>
          <i className="top-bar-split-line"/>
          <span
            className="top-bar-icon-item top-bar-item"
            onClick={onSettingClick}>
            <i
              className="top-bar-icon icon-setting"/>
            <span>{intl.get('setting', language)}</span>
          </span>
          <i className="top-bar-split-line"/>
          <span
            className="top-bar-icon-item top-bar-item"
            onClick={onScreenshotClick}>
            <i
              className="top-bar-icon icon-screen-shot"/>
            <span>{intl.get('screen_shot', language)}</span>
          </span>
          <i className="top-bar-split-line"/>
          <span
            className="top-bar-icon-item top-bar-item"
            onClick={onThemeClick}>
            <i
              style={{ fontSize: 20 }}
              className={`top-bar-icon ${theme === 'dark' ? 'icon-sun' : 'icon-moon'}`}/>
            <span>{theme === 'dark' ? intl.get('light', language) : intl.get('dark', language)}</span>
          </span>
          <i className="top-bar-split-line"/>
          <span
            className="top-bar-icon-item top-bar-item"
            onClick={onLanguageClick}>
            <i className="top-bar-icon icon-earth"/>
            {language === 'zh-CN' ? 'En' : ''}
          </span>
          <i className="top-bar-split-line"/>
        </div>
        <a
          className="version-container"
          target="_blank"
          rel="noopener noreferrer"
          href="https://github.com/liihuu/KLineChart">
          <i
            className="github-icon icon-github"/>
        </a>
      </div>
    )
  }
}
