import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'

import Button from '../button'

import './index.less'

export default class NumberInput extends PureComponent {
  static propTypes = {
    className: PropTypes.string,
    style: PropTypes.object,
    valueClassName: PropTypes.string,
    valueStyle: PropTypes.object,
    defaultValue: PropTypes.any,
    value: PropTypes.any,
    min: PropTypes.number,
    max: PropTypes.number,
    placeholder: PropTypes.string,
    tick: PropTypes.number,
    showTickButton: PropTypes.bool,
    precision: PropTypes.number,
    onFocus: PropTypes.func,
    onChange: PropTypes.func
  }

  static defaultProps = {
    min: Number.MIN_SAFE_INTEGER,
    max: Number.MAX_SAFE_INTEGER,
    tick: 1,
    precision: 0,
    showTickButton: true
  }

  constructor (props) {
    super(props)
    this.state = {
      value: props.value || props.defaultValue
    }
  }

  static getDerivedStateFromProps (props, state) {
    if (('value' in props) && props.value !== state.value) {
      return { value: props.value }
    }
    return null
  }

  onAddClick = () => {
    const { max, tick, onChange } = this.props
    const { value } = this.state
    const v = (+value) + tick
    if (v <= max) {
      onChange && onChange(v)
      this.setState({
        value
      })
    }
  }

  onSubClick = () => {
    const { min, tick, onChange } = this.props
    const { value } = this.state
    const v = (+value) - tick
    if (v >= min) {
      onChange && onChange(v)
      this.setState({
        value
      })
    }
  }

  onValueChange = (e) => {
    const { min, max, precision, onChange } = this.props
    const value = e.target.value
    let reg
    const decimalDigit = Math.max(0, parseInt(precision, 10))
    if (decimalDigit <= 0) {
      reg = new RegExp(/^[1-9]\d*$/)
    } else {
      reg = new RegExp('^\\d+\\.?\\d{0,' + decimalDigit + '}$')
      // reg = new RegExp('/^(0|([1-9][0-9]*))([.][0-9]{0,' + decimalDigit + '})?$/')
    }
    if (value === '' || (reg.test(value) && +value >= min && +value <= max)) {
      onChange && onChange(value)
      this.setState({
        value
      })
    }
  }

  render () {
    const { className, style, valueClassName, valueStyle, showTickButton, placeholder, onFocus } = this.props
    return (
      <div
        style={style}
        className={`number-input ${className}`}>
        <input
          style={valueStyle}
          className={valueClassName}
          placeholder={placeholder}
          value={this.state.value}
          onFocus={onFocus}
          onChange={this.onValueChange}/>
        {
          showTickButton &&
          <div
            className="number-input-button-container">
            <Button
              className="add-arrow-button"
              onClick={this.onAddClick}>
              <i className="add-arrow"/>
            </Button>
            <Button
              onClick={this.onSubClick}>
              <i className="sub-arrow"/>
            </Button>
          </div>
        }
      </div>
    )
  }
}
