import React, { PureComponent, createRef } from 'react'
import { createPortal } from 'react-dom'

import PropTypes from 'prop-types'

import Row from '../row'
import Button from '../button'

import { throttle } from '../utils/performance'

import './index.less'

export default class Modal extends PureComponent {
  static propTypes = {
    className: PropTypes.string,
    style: PropTypes.object,
    innerClassName: PropTypes.string,
    innerStyle: PropTypes.object,
    innerContentStyle: PropTypes.object,
    innerContentClassName: PropTypes.string,
    title: PropTypes.any,
    buttons: PropTypes.array,
    draggable: PropTypes.bool,
    children: PropTypes.any,
    getContainer: PropTypes.func,
    onClose: PropTypes.func
  }

  contentEl = createRef()

  startDragPoint = {}

  startContentElRect = {}

  mouseMove = throttle((e) => {
    const difX = e.clientX - this.startDragPoint.x
    const difY = e.clientY - this.startDragPoint.y
    const oldLeft = this.startContentElRect.left
    const oldTop = this.startContentElRect.top
    let left = oldLeft + difX
    if (left < 0) {
      left = 0
    } else if (left + this.startContentElRect.width) {
    }
    let top = oldTop + difY
    if (top < 0) {
      top = 0
    }
    this.contentEl.current.style.left = `${left}px`
    this.contentEl.current.style.top = `${top}px`
  })

  onTitleMouseDown = (e) => {
    if (this.props.draggable) {
      this.startDragPoint = { x: e.clientX, y: e.clientY }
      this.startContentElRect = this.contentEl.current.getBoundingClientRect()
      this.bindEvent()
    }
  }

  onTitleMouseUp = () => {
    this.unbindEvent()
  }

  bindEvent () {
    document.addEventListener('mousemove', this.mouseMove)
    document.addEventListener('mouseup', this.unbindEvent)
  }

  unbindEvent = () => {
    if (this.props.draggable) {
      document.removeEventListener('mousemove', this.mouseMove)
      document.removeEventListener('mouseup', this.unbindEvent)
    }
  }

  componentDidMount () {
    this.contentEl.current.style.left = `calc(50% - ${this.contentEl.current.clientWidth / 2}px)`
    this.contentEl.current.style.top = `calc(50% - ${this.contentEl.current.clientHeight / 2}px)`
    // setTimeout(() => {
    //   this.contentEl.current.classList.add('modal-inner-show')
    // })
  }

  componentWillUnmount () {
    this.unbindEvent()
  }

  renderButtons () {
    const { buttons } = this.props
    if (buttons) {
      return (
        <Row
          className="modal-inner-button-container">
          {
            buttons.map((button, index) => {
              return (
                <Button
                  key={button.key || `modal_button_${index}`}
                  style={button.style}
                  className={`modal-inner-${button.type}-button ${button.className}`}
                  onClick={button.onClick}>
                  {button.text}
                </Button>
              )
            })
          }
        </Row>
      )
    }
  }

  render () {
    const {
      className, style,
      innerClassName, innerStyle,
      innerContentStyle, innerContentClassName,
      title, children, getContainer, onClose
    } = this.props
    const container = (getContainer && getContainer()) || document.body
    return createPortal(
      <div
        style={style}
        className={`modal ${className}`}>
        <div
          ref={this.contentEl}
          style={innerStyle}
          className={`modal-inner ${innerClassName}`}>
          <Row
            className="modal-inner-title-container"
            onMouseDown={this.onTitleMouseDown}
            onMouseUp={this.onTitleMouseUp}>
            <span>{title && <span>{title}</span>}</span>
            <svg
              className="modal-inner-title-close"
              viewBox="0 0 1024 1024"
              onClick={onClose}>
              <path
                d="M934.184927 199.723787 622.457206 511.452531l311.727721 311.703161c14.334473 14.229073 23.069415 33.951253 23.069415 55.743582 0 43.430138-35.178197 78.660524-78.735226 78.660524-21.664416 0-41.361013-8.865925-55.642275-23.069415L511.149121 622.838388 199.420377 934.490384c-14.204513 14.20349-33.901111 23.069415-55.642275 23.069415-43.482327 0-78.737272-35.230386-78.737272-78.660524 0-21.792329 8.864902-41.513486 23.094998-55.743582l311.677579-311.703161L88.135828 199.723787c-14.230096-14.255679-23.094998-33.92567-23.094998-55.642275 0-43.430138 35.254945-78.762855 78.737272-78.762855 21.741163 0 41.437761 8.813736 55.642275 23.069415l311.727721 311.727721L822.876842 88.389096c14.281261-14.255679 33.977859-23.069415 55.642275-23.069415 43.557028 0 78.735226 35.332716 78.735226 78.762855C957.254342 165.798117 948.5194 185.468109 934.184927 199.723787" />
            </svg>
          </Row>
          <div
            style={innerContentStyle}
            className={`modal-inner-content-container ${innerContentClassName}`}>
            {children}
          </div>
          {this.renderButtons()}
        </div>
      </div>,
      container
    )
  }
}
