import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'

import Col from '../../../../component/col'
import Row from '../../../../component/row'

import { mapping } from './icon'

import './tools.less'

export default class Tools extends PureComponent {
  static propTypes = {
    theme: PropTypes.string,
    language: PropTypes.string,
    onToolsClick: PropTypes.func,
    onToolsDeleteClick: PropTypes.func
  }

  static defaultProps = {
    theme: 'night'
  }

  state = {
    singleLine: 'segment',
    moreLine: 'parallelStraightLine',
    fibonacci: 'fibonacciLine',
    polygon: 'rect',
    wave: 'threeWaves',
    showExtensionGraphicMarkKey: ''
  }

  showExtensionGraphicMark = (key) => () => {
    const showExtensionGraphicMarkKey = this.state.showExtensionGraphicMarkKey
    if (key === showExtensionGraphicMarkKey) {
      this.setState({
        showExtensionGraphicMarkKey: ''
      })
    } else {
      this.setState({
        showExtensionGraphicMarkKey: key
      })
    }
  }

  onArrowBlur = () => {
    this.setState({
      showExtensionGraphicMarkKey: ''
    })
  }

  onExtensionGraphicMarkRowClick = (type, key) => (e) => {
    e.stopPropagation()
    this.setState({
      showExtensionGraphicMarkKey: '',
      [type]: key
    })
    this.onToolsClick(key)()
  }

  onToolsClick = (key) => () => {
    const { onToolsClick } = this.props
    onToolsClick && onToolsClick(key)
  }

  render () {
    const { theme, onToolsDeleteClick } = this.props
    return (
      <Col
        className={`tools-${theme}`}>
        <ul
          className="tools-list">
          {
            [
              'horizontalStraightLine',
              'horizontalRayLine',
              'horizontalSegment',
              'verticalStraightLine',
              'verticalRayLine',
              'verticalSegment',
              'straightLine',
              'rayLine',
              'segment',
              'priceLine',
              'priceChannelLine',
              'parallelStraightLine',
              'fibonacciLine'
            ].map(key => {
              return (
                <li
                  key={key}
                  className="tools-list-row">
                  <Row
                    className="tools-list-row-icon-wrapper"
                    onClick={this.onToolsClick(key)}>
                    {mapping[key]}
                  </Row>
                </li>
              )
            })
          }
          <li
            className="tools-list-row tools-list-delete-row">
            <Row
              className="tools-list-row-icon-wrapper"
              onClick={onToolsDeleteClick}>
              <svg
                className="tools-list-row-icon"
                viewBox="0 0 1024 1024">
                <path
                  d="M845.12256 287.55456v572.62592c0 15.77984 10.89024 86.44096-15.2576 86.44096H247.7568c-11.74528 0-36.38784 4.56704-37.92384-12.53888-0.90112-10.0352 0-20.57216 0-30.63296V287.55968c0-33.01888-51.2-33.01888-51.2 0v572.62592c0 44.94848-10.20416 99.40992 31.56992 128.94208 15.00672 10.60352 35.80928 8.69888 53.31968 8.69888h531.38432c48.04096 0 121.41568 6.58432 121.41568-63.7952V287.55968c0-33.024-51.2-33.024-51.2-0.00512z"/>
                <path
                  d="M501.86752 420.46976v361.33888c0 33.01376 51.22048 33.01376 51.22048 0V420.46976c0-33.01888-51.22048-33.01888-51.22048 0zM325.35552 420.46976v361.33888c0 33.01376 51.2 33.01376 51.2 0V420.46976c0-33.01888-51.2-33.01888-51.2 0zM676.00384 420.46976v361.33888c0 33.01376 51.2 33.01376 51.2 0V420.46976c0-33.01888-51.2-33.01888-51.2 0zM102.5024 217.5232h849.95584c33.01376 0 33.01376-51.2 0-51.2H102.5024c-33.01888 0-33.01888 51.2 0 51.2z"/>
                <path
                  d="M338.7648 174.82752V94.53056c0-18.37056 1.00864-30.9248 21.6832-30.9248h334.0544c20.67456 0 21.6832 12.55424 21.6832 30.9248v80.29696c0 33.01888 51.2 33.01888 51.2 0 0-54.73792 18.51392-162.42176-65.26464-162.42176H352.82432c-83.77344 0-65.25952 107.68384-65.25952 162.42176 0 33.01888 51.2 33.01888 51.2 0z"/>
              </svg>
            </Row>
          </li>
        </ul>
      </Col>
    )
  }
}
