import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'

import { init, dispose } from 'klinecharts'

// import {
//   arrow,
//   circle, rect, triangle, parallelogram,
//   fibonacciCircle, fibonacciSegment, fibonacciSpiral,
//   fibonacciSpeedResistanceFan, fibonacciExtension, gannBox,
//   threeWaves, fiveWaves, eightWaves, abcd, xabcd
// } from './graphicmarkextension'

import Loading from '../../../../component/loading'

import './chart-widget.less'

// extension.addGraphicMark([
//   arrow,
//   circle, rect, triangle, parallelogram,
//   fibonacciCircle, fibonacciSegment, fibonacciSpiral,
//   fibonacciSpeedResistanceFan, fibonacciExtension, gannBox,
//   threeWaves, fiveWaves, eightWaves, abcd, xabcd
// ])

export default class ChartWidget extends PureComponent {
  static propTypes = {
    theme: PropTypes.string,
    language: PropTypes.string,
    domId: PropTypes.string,
    status: PropTypes.string,
    symbol: PropTypes.string,
    exchange: PropTypes.string,
    period: PropTypes.string,
    timezone: PropTypes.string,
    pricePrecision: PropTypes.number,
    volumePrecision: PropTypes.number,
    chartType: PropTypes.string,
    onChartReady: PropTypes.func,
    onStyleChange: PropTypes.func,
    onLoadHistoryData: PropTypes.func,
    subscribe: PropTypes.func,
    unsubscribe: PropTypes.func
  }

  static defaultProps = {
    theme: 'light',
    domId: 'k-line-chart',
    symbol: '',
    period: '',
    exchange: '',
    timezone: 'Asia/Shanghai',
    pricePrecision: 2,
    volumePrecision: 0
  }

  windowResize = () => {
    if (this.chart) {
      this.chart.resize()
    }
  }

  state = {
    loading: true
  }

  componentDidMount () {
    const {
      domId, timezone,
      theme, language,
      pricePrecision,
      volumePrecision,
      onChartReady
    } = this.props
    this.chart = init(domId, this.getStyles(theme, language))
    this.chart.setTimezone(timezone)
    this.chart.setPriceVolumePrecision(pricePrecision, volumePrecision)
    this.chart.loadMore(timestamp => {
      this.loadMoreHistoryData(timestamp)
    })
    window.addEventListener('resize', this.windowResize, false)
    onChartReady && onChartReady(this.chart)
    this.firstLoadHistoryData()
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    const {
      theme, language, status, symbol, exchange, period,
      pricePrecision, volumePrecision,
      timezone, chartType,
      unsubscribe, onStyleChange
    } = this.props
    if (this.chart && (prevProps.theme !== theme || prevProps.language !== language)) {
      this.chart.setStyleOptions(this.getStyles(theme, language))
      onStyleChange && onStyleChange()
    }
    if (prevProps.timezone !== timezone) {
      this.chart.setTimezone(timezone)
    }
    if (
      prevProps.pricePrecision !== pricePrecision ||
      prevProps.volumePrecision !== volumePrecision
    ) {
      this.chart.setPriceVolumePrecision(pricePrecision, volumePrecision)
    }
    if (prevProps.chartType !== chartType) {
      if (chartType === 'line') {
        this.chart.setStyleOptions({
          candle: { type: 'area' }
        })
      } else {
        this.chart.setStyleOptions({
          candle: { type: 'candle_solid' }
        })
      }
    }
    if (status === 'ok' &&
      (
        prevProps.status !== status ||
        prevProps.symbol !== symbol ||
        prevProps.period !== period ||
        prevProps.exchange !== exchange
      )
    ) {
      unsubscribe && unsubscribe(prevProps.symbol, prevProps.exchange, prevProps.period)
      this.firstLoadHistoryData()
    }
  }

  /**
   * 
   */
  firstLoadHistoryData () {
    const { status, symbol, exchange, period, onLoadHistoryData, subscribe } = this.props
    if (status === 'ok' && symbol && exchange && period) {
      this.setState({
        loading: true
      })
      const onHistoryCallback = (dataList, isMore) => {
        this.setState({
          loading: false
        })
        this.chart && this.chart.applyNewData(dataList, isMore)
        const onRealtimeCallback = (data) => {
          this.chart && this.chart.updateData(data)
        }
        subscribe && subscribe(symbol, exchange, period, onRealtimeCallback)
      }
      const onErrorCallback = _ => {
        this.setState({
          loading: false
        })
      }
      onLoadHistoryData && onLoadHistoryData(symbol, exchange, period, Date.now(), onHistoryCallback, onErrorCallback, true)
    }
  }

  /**
   * 
   * @param timestamp
   */
  loadMoreHistoryData (timestamp) {
    const { symbol, exchange, period, onLoadHistoryData } = this.props
    if (symbol && period) {
      const onHistoryCallback = (dataList, isMore) => {
        this.chart && this.chart.applyMoreData(dataList, isMore)
      }
      const onErrorCallback = _ => {
      }
      onLoadHistoryData && onLoadHistoryData(symbol, exchange, period, timestamp, onHistoryCallback, onErrorCallback, false)
    }
  }

  /**
   * 
   */
  getStyles (theme, language) {
    const isMobile = /(iPhone|iPad|iPod|iOS|Android)/i.test(navigator.userAgent)
    const isLight = theme === 'light'
    const mainColor = isLight ? '#76808F' : '#929AA5'
    const gridColor = isLight ? '#ededed' : '#292929'
    const crossTextFillColor = isLight ? '#686d76' : '#373a40'
    const crossTextColor = isLight ? '#FFFFFF' : '#FFFFFF'
    const axisLineColor = isLight ? '#DDDDDD' : '#333333'
    const axisTextColor = isLight ? '#76808F' : '#929AA5'
    return {
      grid: {
        horizontal: {
          color: gridColor
        },
        vertical: {
          display: true,
          color: gridColor
        }
      },
      candle: {
        bar: {
          noChangeColor: '#26A69A'
        },
        priceMark: {
          high: {
            color: mainColor,
            textFamily: 'Roboto'
          },
          low: {
            color: mainColor,
            textFamily: 'Roboto'
          },
          last: {
            display: false
          }
        },
        tooltip: {
          showType: isMobile ? 'rect' : 'standard',
          showRule: isMobile ? 'follow_cross' : 'always',
          labels: language === 'zh-CN' ? [': ', ': ', ': ', ': ', ': ', ': '] : ['T: ', 'O: ', 'C: ', 'H: ', 'L: ', 'V: '],
          rect: {
            fillColor: isLight ? 'rgba(17, 17, 17, .6)' : 'rgba(10, 10, 10, .6)',
            borderColor: isLight ? 'rgba(17, 17, 17, .6)' : 'rgba(10, 10, 10, .6)'
          },
          text: {
            color: mainColor,
            family: 'Roboto'
          }
        }
      },
      technicalIndicator: {
        bar: {
          upColor: 'rgba(38, 166, 154, .6)',
          downColor: 'rgba(239, 83, 80, .6)',
          noChangeColor: 'rgba(38, 166, 154, .6)'
        },
        circle: {
          upColor: 'rgba(38, 166, 154, .6)',
          downColor: 'rgba(239, 83, 80, .6)',
          noChangeColor: 'rgba(38, 166, 154, .6)'
        },
        lastValueMark: {
          show: true,
          text: {
            show: true
          }
        },
        tooltip: {
          showType: isMobile ? 'rect' : 'standard',
          showRule: isMobile ? 'follow_cross' : 'always',
          text: {
            color: mainColor,
            family: 'Roboto'
          }
        }
      },
      xAxis: {
        minHeight: 26,
        axisLine: {
          color: axisLineColor
        },
        tickText: {
          color: axisTextColor,
          family: 'Roboto'
        },
        tickLine: {
          color: axisLineColor
        }
      },
      yAxis: {
        inside: isMobile,
        axisLine: {
          color: axisLineColor
        },
        tickText: {
          color: axisTextColor,
          family: 'Roboto'
        },
        tickLine: {
          color: axisLineColor
        }
      },
      separator: {
        color: axisLineColor
      },
      crosshair: {
        horizontal: {
          line: {
            color: mainColor
          },
          text: {
            color: crossTextColor,
            borderColor: crossTextFillColor,
            backgroundColor: crossTextFillColor,
            family: 'Roboto'
          }
        },
        vertical: {
          line: {
            color: mainColor
          },
          text: {
            color: crossTextColor,
            borderColor: crossTextFillColor,
            backgroundColor: crossTextFillColor,
            family: 'Roboto'
          }
        }
      }
    }
  }

  componentWillUnmount () {
    dispose(this.props.domId)
  }

  render () {
    const { domId } = this.props
    return (
      <div
        className="chart-widget">
        <div
          style={{ width: '100%', height: '100%' }}
          id={domId}/>
        {
          this.state.loading && <Loading/>
        }
      </div>
    )
  }
}
