import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'

import lodash from 'lodash/object'

import Modal from '../../../../component/modal'
import Button from '../../../../component/button'
import ColorPicker from '../../../../component/colorpicker'
import RadioGroup from '../../../../component/radiogroup'
import Select from '../../../../component/select'
import Checkbox from '../../../../component/checkbox'
import NumberInput from '../../../../component/numberinput'

import intl from '../../../../component/intl'

import {
  settingMenus,
  getCandleStyles,
  getAxisStyles, getChartBackgroundStyles,
  getTimezones
} from './config'

export default class SettingModal extends PureComponent {
  static propTypes = {
    defaultStyleOptions: PropTypes.object,
    styleOptions: PropTypes.object,
    language: PropTypes.string,
    timezone: PropTypes.string,
    onStyleOptionsChange: PropTypes.func,
    onTimezoneChange: PropTypes.func,
    onClose: PropTypes.func
  }

  state = {
    currentMenu: settingMenus[0]
  }

  getStyles () {
    const { currentMenu } = this.state
    switch (currentMenu) {
      case 'main_view_style': return getCandleStyles()
      case 'chart_background': return getChartBackgroundStyles()
      case 'axis': return getAxisStyles()
      case 'timezone': return getTimezones()
    }
    return []
  }

  changeMenu = (menu) => () => {
    this.setState({
      currentMenu: menu
    })
  }

  resetClick = () => {
    const { defaultStyleOptions, onStyleOptionsChange } = this.props
    onStyleOptionsChange && onStyleOptionsChange(lodash.assign({}, defaultStyleOptions))
  }

  onComponentChange = (componentType, key) => (data) => {
    const { styleOptions, onStyleOptionsChange, onTimezoneChange } = this.props
    if (key === 'timezone') {
      onTimezoneChange(data)
    } else {
      const options = lodash.assign({}, styleOptions)
      switch (componentType) {
        case 'radioGroup': {
          lodash.set(options, key, data.value)
          break
        }
        case 'select': {
          lodash.set(options, key, data.key)
          break
        }
        case 'color':
        case 'checkbox': {
          lodash.set(options, key, data)
          break
        }
        case 'input': {
          lodash.set(options, key, +data)
          break
        }
      }
      onStyleOptionsChange && onStyleOptionsChange(options)
    }
  }

  renderComponent (style) {
    const { styleOptions, timezone } = this.props
    switch (style.componentType) {
      case 'compose': {
        return style.dataSource.map(data => {
          return this.renderComponent(data)
        })
      }
      case 'radioGroup': {
        return (
          <RadioGroup
            key={style.key}
            value={lodash.get(styleOptions, style.key)}
            radioStyle={style.radioStyle}
            dataSource={style.dataSource}
            onChange={this.onComponentChange('radioGroup', style.key)}/>
        )
      }
      case 'colorGroup': {
        return style.dataSource.map((data, index) => {
          return (
            <ColorPicker
              key={`color_${index}`}
              color={lodash.get(styleOptions, data.key)}
              style={data.style}
              label={data.label}
              onChange={this.onComponentChange('color', data.key)}/>
          )
        })
      }
      case 'color': {
        return (
          <ColorPicker
            key={style.key}
            style={style.style}
            color={lodash.get(styleOptions, style.key)}
            onChange={this.onComponentChange('color', style.key)}/>
        )
      }
      case 'select': {
        return (
          <Select
            key={style.key}
            style={style.style}
            value={style.key === 'timezone' ? timezone : intl.get(lodash.get(styleOptions, style.key))}
            dataSource={style.dataSource}
            onSelected={this.onComponentChange('select', style.key)}/>
        )
      }
      case 'checkbox': {
        return (
          <Checkbox
            key={style.key}
            style={style.style}
            checked={lodash.get(styleOptions, style.key)}
            label={intl.get(style.label)}
            onChange={this.onComponentChange('checkbox', style.key)}/>
        )
      }
      case 'input': {
        return (
          <NumberInput
            key={style.key}
            style={style.style}
            min={1}
            max={50}
            value={lodash.get(styleOptions, style.key)}
            onChange={this.onComponentChange('input', style.key)}/>
        )
      }
    }
  }

  render () {
    const { onClose, language } = this.props
    const { currentMenu } = this.state
    return (
      <Modal
        draggable
        innerContentStyle={{ padding: 0 }}
        onClose={onClose}
        title={intl.get('setting')}>
        <div
          className="modal-content">
          <ul
            style={{ height: 400 }}
            className="modal-content-list-container modal-content-left-container">
            {
              settingMenus.map(menu => {
                return (
                  <li
                    key={menu}
                    className={currentMenu === menu ? 'modal-content-list-row-selected' : ''}
                    onClick={this.changeMenu(menu)}>
                    {intl.get(menu)}
                  </li>
                )
              })
            }
          </ul>
          <ul
            className="modal-content-list-container modal-content-setting-container">
            {
              this.getStyles().map((style) => {
                return (
                  <li
                    key={style.key}
                    className="modal-content-setting-list-content-row">
                    {
                      style.title &&
                      <span
                        className="modal-content-setting-row-title">
                        {intl.get(style.title)}
                      </span>
                    }
                    {this.renderComponent(style)}
                  </li>
                )
              })
            }
          </ul>
          {
            currentMenu !== 'timezone' &&
            <div
              className="modal-content-button-container">
              <Button
                className="modal-content-reset-button"
                onClick={this.resetClick}>
                {intl.get('reset', language)}
              </Button>
            </div>
          }
        </div>
      </Modal>
    )
  }
}
