import React, { PureComponent } from 'react'
import { OverlayScrollbarsComponent } from 'overlayscrollbars-react'

import PropTypes from 'prop-types'

import { isObject } from '../utils/typeChecks'

import './index.less'

export default class Select extends PureComponent {
  static propTypes = {
    style: PropTypes.object,
    className: PropTypes.string,
    selectorStyle: PropTypes.object,
    selectorClassName: PropTypes.string,
    arrowStyle: PropTypes.object,
    arrowClassName: PropTypes.string,
    valueStyle: PropTypes.object,
    valueClassName: PropTypes.string,
    dropDownContainerStyle: PropTypes.object,
    dropDownContainerClassName: PropTypes.string,
    dropDownRowStyle: PropTypes.object,
    dropDownRowClassName: PropTypes.string,
    trigger: PropTypes.oneOf(['hover', 'click']),
    valueIcon: PropTypes.any,
    defaultValue: PropTypes.any,
    valueKey: PropTypes.any,
    value: PropTypes.any,
    defaultOpen: PropTypes.bool,
    open: PropTypes.bool,
    showArrow: PropTypes.bool,
    dataSource: PropTypes.array,
    renderDropDownContainer: PropTypes.func,
    renderDropDownRow: PropTypes.func,
    onDropdownVisibleChange: PropTypes.func,
    onSelected: PropTypes.func
  }

  static defaultProps = {
    trigger: 'click',
    valueKey: 'text',
    showArrow: true,
    dataSource: []
  }

  constructor (props) {
    super(props)
    this.state = {
      value: props.value || props.defaultValue,
      open: props.open || props.defaultOpen
    }
  }

  static getDerivedStateFromProps (props, state) {
    let nextState = null
    if (('value' in props) && props.value !== state.value) {
      nextState = { value: props.value }
    }
    if (('open' in props) && props.open !== state.open) {
      if (nextState) {
        nextState.open = props.open
      } else {
        nextState = { open: props.open }
      }
    }
    return nextState
  }

  getDataValue (data) {
    return isObject(data) ? data[this.props.valueKey] : data
  }

  onSelectClick = () => {
    const { trigger, onDropdownVisibleChange } = this.props
    if (trigger === 'click') {
      const open = !this.state.open
      onDropdownVisibleChange && onDropdownVisibleChange(open)
      this.setState({
        open
      })
    }
  }

  onSelectMouseEnter = () => {
    const { trigger, onDropdownVisibleChange } = this.props
    if (trigger === 'hover') {
      onDropdownVisibleChange && onDropdownVisibleChange(true)
      this.setState({
        open: true
      })
    }
  }

  onSelectMouseLeave = () => {
    const { trigger, onDropdownVisibleChange } = this.props
    if (trigger === 'hover') {
      onDropdownVisibleChange && onDropdownVisibleChange(false)
      this.setState({
        open: false
      })
    }
  }

  onSelectBlur = () => {
    const { onDropdownVisibleChange } = this.props
    onDropdownVisibleChange && onDropdownVisibleChange(false)
    this.setState({
      open: false
    })
  }

  onRowClick = (data, value) => () => {
    const { onSelected } = this.props
    const state = {
      open: false
    }
    if (this.state.value !== value) {
      onSelected && onSelected(data)
      state.value = value
    }
    this.setState(state)
  }

  renderDropDownList () {
    const { dropDownRowStyle, dropDownRowClassName, dataSource, renderDropDownRow } = this.props
    return dataSource.map((data, index) => {
      const value = this.getDataValue(data)
      return (
        <li
          key={`row_${index}`}
          style={dropDownRowStyle}
          className={dropDownRowClassName}
          onClick={this.onRowClick(data, value)}>
          {renderDropDownRow ? renderDropDownRow(data, index) : value}
        </li>
      )
    })
  }

  render () {
    const {
      style, className, dropDownContainerClassName, dropDownContainerStyle,
      selectorStyle, selectorClassName,
      arrowStyle, arrowClassName,
      valueStyle, valueClassName,
      valueIcon, showArrow, renderDropDownContainer
    } = this.props
    const { value, open } = this.state
    return (
      <div
        style={style}
        className={`select ${className} ${open ? 'select-show' : ''}`}
        tabIndex="0"
        onClick={this.onSelectClick}
        onBlur={this.onSelectBlur}
        onMouseEnter={this.onSelectMouseEnter}
        onMouseLeave={this.onSelectMouseLeave}>
        <div
          style={selectorStyle}
          className={`select-selector-container ${selectorClassName}`}>
          <div
            className="select-selector-left-container">
            {valueIcon}
            <span
              style={valueStyle}
              className={`select-selector-value ${valueClassName}`}>
              {value}
            </span>
          </div>
          {
            showArrow && <i style={arrowStyle} className={`select-selector-arrow ${arrowClassName}`}/>
          }
        </div>
        <div
          style={dropDownContainerStyle}
          className={`${dropDownContainerClassName} select-drop-down-container`}>
          <OverlayScrollbarsComponent
            style={{ maxHeight: 200 }}>
            {
              renderDropDownContainer
                ? renderDropDownContainer()
                : (
                  <ul>
                    {this.renderDropDownList()}
                  </ul>
                )
            }
          </OverlayScrollbarsComponent>
        </div>
      </div>
    )
  }
}
