import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'

import Row from '../../../component/row'
import Tools from './tools/Tools'
import ChartWidget from './chartwidget/ChartWidget'
import { SettingModal, TechnicalIndicatorModal } from './modal'
import intl from '../../../component/intl'

import './stock-chart.less'

export default class StockChart extends PureComponent {
  static propTypes = {
    className: PropTypes.string,
    style: PropTypes.object,
    theme: PropTypes.string,
    domId: PropTypes.string,
    status: PropTypes.string,
    symbol: PropTypes.string,
    exchange: PropTypes.string,
    period: PropTypes.string,
    pricePrecision: PropTypes.number,
    volumePrecision: PropTypes.number,
    chartType: PropTypes.string,
    defaultMainTechnicalIndicators: PropTypes.array,
    defaultSubTechnicalIndicators: PropTypes.array,
    showTechnicalIndicatorModal: PropTypes.bool,
    showSettingModal: PropTypes.bool,
    onChartReady: PropTypes.func,
    onLoadHistoryData: PropTypes.func,
    subscribe: PropTypes.func,
    unsubscribe: PropTypes.func,
    onTechnicalIndicatorModalClose: PropTypes.func,
    onSettingModalClose: PropTypes.func
  }

  static defaultProps = {
    theme: 'light',
    defaultMainTechnicalIndicators: [],
    defaultSubTechnicalIndicators: []
  }

  constructor (props) {
    super(props)
    this.paneIds = {}
    this.state = {
      defaultStyleOptions: {},
      styleOptions: {},
      defaultTechnicalIndicatorParams: {},
      mainTechnicalIndicators: [...props.defaultMainTechnicalIndicators],
      subTechnicalIndicators: [...props.defaultSubTechnicalIndicators],
      technicalIndicatorParams: {},
      timezone: { key: 'Asia/Shanghai', text: intl.get('shanghai') }
    }
  }

  componentDidUpdate (prevProps, prevState, snapshot) {
    const { chartType } = this.props
    if (chartType !== prevProps.chartType) {
      if (chartType === 'line') {
        this.chart && this.chart.createTechnicalIndicator('AVP', false, { id: 'candle_pane' })
      } else {
        const mainTechnicalIndicators = this.state.mainTechnicalIndicators
        if (mainTechnicalIndicators.length > 0) {
          this.chart && this.chart.createTechnicalIndicator(mainTechnicalIndicators[0], false, { id: 'candle_pane' })
          for (let i = 1; i < mainTechnicalIndicators.length; i++) {
            this.chart && this.chart.createTechnicalIndicator(mainTechnicalIndicators[i], true, { id: 'candle_pane' })
          }
        } else {
          this.chart && this.chart.removeTechnicalIndicator('candle_pane', 'AVP')
        }
      }
    }
  }

  onChartReady = chart => {
    const { defaultMainTechnicalIndicators, defaultSubTechnicalIndicators } = this.props
    this.chart = chart
    defaultMainTechnicalIndicators.forEach(name => {
      this.chart.createTechnicalIndicator(name, true, { id: 'candle_pane' })
    })
    defaultSubTechnicalIndicators.forEach(name => {
      this.paneIds[name] = this.chart.createTechnicalIndicator(name, false, { height: 120 })
    })
    const technicalIndicatorInfo = this.chart.getTechnicalIndicatorByName()
    const defaultParams = {}
    const params = {}
    for (const key in technicalIndicatorInfo) {
      defaultParams[key] = technicalIndicatorInfo[key].calcParams
      params[key] = technicalIndicatorInfo[key].calcParams
    }
    this.setState({
      defaultStyleOptions: (this.chart.getStyleOptions()) || {},
      styleOptions: (this.chart.getStyleOptions()) || {},
      defaultTechnicalIndicatorParams: defaultParams,
      technicalIndicatorParams: params
    })
    this.props.onChartReady && this.props.onChartReady(chart)
  }

  onChartStyleChange = () => {
    this.setState({
      defaultStyleOptions: (this.chart && this.chart.getStyleOptions()) || {},
      styleOptions: (this.chart && this.chart.getStyleOptions()) || {}
    })
  }

  onToolsClick = (name) => {
    if (this.chart) {
      this.chart.createGraphicMark(name)
    }
  }

  onToolsDeleteClick = () => {
    this.chart && this.chart.removeGraphicMark()
  }

  onSettingStyleChange = (options) => {
    this.chart && this.chart.setStyleOptions(options)
    this.setState({
      styleOptions: options
    })
  }

  onTimezoneChange = (zone) => {
    this.setState({
      timezone: zone
    })
  }

  onMainTechnicalIndicatorChange = (name, controlType) => {
    const mainTechnicalIndicators = [...this.state.mainTechnicalIndicators]
    if (controlType === 'delete') {
      if (this.chart) {
        this.chart.removeTechnicalIndicator('candle_pane', name)
        mainTechnicalIndicators.splice(mainTechnicalIndicators.indexOf(name), 1)
      }
    } else {
      if (this.chart) {
        this.chart.createTechnicalIndicator(name, true, { id: 'candle_pane' })
        mainTechnicalIndicators.push(name)
      }
    }
    this.setState({
      mainTechnicalIndicators
    })
  }

  onSubTechnicalIndicatorChange = (name, controlType) => {
    const subTechnicalIndicators = [...this.state.subTechnicalIndicators]
    if (controlType === 'delete') {
      if (this.chart) {
        const paneId = this.paneIds[name]
        this.chart.removeTechnicalIndicator(paneId, name)
        subTechnicalIndicators.splice(subTechnicalIndicators.indexOf(name), 1)
        delete this.paneIds[name]
      }
    } else {
      if (this.chart) {
        this.paneIds[name] = this.chart.createTechnicalIndicator(name, false, { height: 120 })
        subTechnicalIndicators.push(name)
      }
    }
    this.setState({
      subTechnicalIndicators
    })
  }

  onTechnicalIndicatorParamsChange = (type, params) => {
    const technicalIndicatorParams = { ...this.state.technicalIndicatorParams }
    technicalIndicatorParams[type] = params
    this.setState({
      technicalIndicatorParams
    })
  }

  onTechnicalIndicatorParamsConfirmClick = (name) => {
    const params = this.state.technicalIndicatorParams[name] || []
    const calcParams = []
    params.forEach(param => {
      if (param) {
        calcParams.push(param)
      }
    })
    this.chart && this.chart.overrideTechnicalIndicator({ name, calcParams })
  }

  render () {
    const {
      className, style,
      theme, language, domId, status,
      symbol, exchange, period, chartType,
      showTechnicalIndicatorModal, showSettingModal,
      onLoadHistoryData, subscribe, unsubscribe,
      onTechnicalIndicatorModalClose, onSettingModalClose
    } = this.props
    const {
      mainTechnicalIndicators, subTechnicalIndicators,
      defaultStyleOptions, styleOptions,
      defaultTechnicalIndicatorParams,
      technicalIndicatorParams,
      timezone
    } = this.state
    return (
      <Row
        style={style}
        className={`stock-chart ${className}`}>
        {
          showSettingModal &&
          <SettingModal
            language={language}
            defaultStyleOptions={defaultStyleOptions}
            styleOptions={styleOptions}
            timezone={timezone.text}
            onStyleOptionsChange={this.onSettingStyleChange}
            onTimezoneChange={this.onTimezoneChange}
            onClose={onSettingModalClose}
          />
        }
        {
          showTechnicalIndicatorModal &&
          <TechnicalIndicatorModal
            language={language}
            defaultTechnicalIndicatorParams={defaultTechnicalIndicatorParams}
            technicalIndicatorParams={technicalIndicatorParams}
            mainTechnicalIndicators={mainTechnicalIndicators}
            subTechnicalIndicators={subTechnicalIndicators}
            onMainTechnicalIndicatorChange={this.onMainTechnicalIndicatorChange}
            onSubTechnicalIndicatorChange={this.onSubTechnicalIndicatorChange}
            onTechnicalIndicatorParamsChange={this.onTechnicalIndicatorParamsChange}
            onTechnicalIndicatorParamsConfirmClick={this.onTechnicalIndicatorParamsConfirmClick}
            onClose={onTechnicalIndicatorModalClose}
          />
        }
        <Tools
          theme={theme}
          language={language}
          onToolsClick={this.onToolsClick}
          onToolsDeleteClick={this.onToolsDeleteClick}
        />
        <ChartWidget
          theme={theme}
          language={language}
          domId={domId}
          status={status}
          symbol={symbol}
          period={period}
          exchange={exchange}
          timezone={timezone.key}
          chartType={chartType}
          onStyleChange={this.onChartStyleChange}
          onChartReady={this.onChartReady}
          onLoadHistoryData={onLoadHistoryData}
          subscribe={subscribe}
          unsubscribe={unsubscribe}
        />
      </Row>
    )
  }
}
