import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import { isObject } from '../utils/typeChecks'

import Radio from '../radio'

import './index.less'

export default class RadioGroup extends PureComponent {
  static propTypes = {
    className: PropTypes.string,
    style: PropTypes.object,
    radioClassName: PropTypes.string,
    radioStyle: PropTypes.object,
    radioIconClassName: PropTypes.string,
    radioIconStyle: PropTypes.object,
    radioLabelClassName: PropTypes.string,
    radioLabelStyle: PropTypes.object,
    direction: PropTypes.string,
    dataSource: PropTypes.array,
    defaultValue: PropTypes.any,
    value: PropTypes.any,
    valueKey: PropTypes.any,
    labelKey: PropTypes.any,
    onChange: PropTypes.func
  }

  static defaultProps = {
    valueKey: 'value',
    labelKey: 'label',
    dataSource: []
  }

  constructor (props) {
    super(props)
    this.state = {
      value: props.value || props.defaultValue
    }
  }

  static getDerivedStateFromProps (props, state) {
    if (('value' in props) && props.value !== state.value) {
      return { value: props.value }
    }
    return null
  }

  onRadioChange = (data) => () => {
    const { valueKey, onChange } = this.props
    const value = this.getValue(data, valueKey)
    if (value !== this.state.value) {
      onChange && onChange(data)
      this.setState({
        value
      })
    }
  }

  getValue (data, key) {
    if (isObject(data)) {
      return data[key]
    }
    return data
  }

  render () {
    const {
      className, style,
      radioClassName, radioStyle,
      radioIconClassName, radioIconStyle,
      radioLabelClassName, radioLabelStyle,
      labelKey, valueKey, dataSource
    } = this.props
    const { value } = this.state
    return (
      <div
        style={style}
        className={`radio-group ${className}`}>
        {
          dataSource.map((data, index) => {
            const checked = this.getValue(data, valueKey) === value
            return (
              <Radio
                key={`key_${index}`}
                className={radioClassName}
                style={radioStyle}
                iconClassName={radioIconClassName}
                iconStyle={radioIconStyle}
                labelClassName={radioLabelClassName}
                labelStyle={radioLabelStyle}
                checked={checked}
                label={this.getValue(data, labelKey)}
                onChange={this.onRadioChange(data)}/>
            )
          })
        }
      </div>
    )
  }
}
