import React, { PureComponent } from 'react'

import TopBar from './topbar/TopBar'
import StockChart from './stockchart/StockChart'

import asyncData from './datasource/asyncData'

import translatePeriodToMillisecond from './datasource/translatePeriodToMillisecond'

import './k-line-chart.less'

export default class KLineChart extends PureComponent {
  constructor (props) {
    super(props)
    this.state = {
      theme: 'dark',
      language: 'zh-CN',
      currentPeriod: '15m',
      showSettingModal: false,
      showTechnicalIndicatorModal: false
    }
  }

  onChartReady = (chart) => {
    this.kLineChart = chart
    // this.kLineChart.setOffsetRightSpace(200)
    this.kLineChart.subscribeAction('drawCandle', (data) => {
      const { language } = this.state
      const { ctx, dataIndex, kLineData, coordinate, isCandle } = data
      const dataSize = this.kLineChart.getDataList().length
      if (isCandle && dataIndex === dataSize - 1) {
        ctx.font = '12px Roboto'
        let text
        if (kLineData.close > kLineData.open) {
          text = language === 'zh-CN' ? '   ' : '   Up! '
        } else if (kLineData.close < kLineData.open) {
          text = language === 'zh-CN' ? '   ' : '   Fall! '
        } else {
          text = language === 'zh-CN' ? '' : 'Nothing! '
        }
        const textWidth = ctx.measureText(text).width

        const startX = coordinate.x + 10
        const startY = coordinate.open
        ctx.beginPath()
        ctx.moveTo(startX, startY)
        ctx.lineTo(startX + 10, startY - 10)
        ctx.lineTo(startX + 10 + textWidth + 10, startY - 10)
        ctx.lineTo(startX + 10 + textWidth + 10, startY + 10)
        ctx.lineTo(startX + 10, startY + 10)
        ctx.closePath()
        ctx.fill()
        ctx.textBaseline = 'middle'
        ctx.fillStyle = '#fff'
        ctx.fillText(text, startX + 12, startY)
      }

      if (kLineData.mark && isCandle) {
        ctx.font = '12px Roboto'
        ctx.fillStyle = '#2d6187'
        ctx.strokeStyle = '#2d6187'
        const text = language === 'zh-CN' ? `${kLineData.mark}` : `Mark! ${kLineData.mark}`
        const textWidth = ctx.measureText(text).width
        const startX = coordinate.x
        let startY = coordinate.high - 6
        ctx.setLineDash([3, 3])
        ctx.beginPath()
        ctx.moveTo(startX, startY)
        ctx.lineTo(startX, startY - 50)
        ctx.closePath()
        ctx.stroke()
        startY -= 50
        ctx.beginPath()
        ctx.moveTo(startX, startY)
        ctx.lineTo(startX - 4, startY - 5)
        ctx.lineTo(startX + 4, startY - 5)
        ctx.closePath()
        ctx.fill()

        const rectX = startX - textWidth / 2 - 6
        const rectY = startY - 5 - 28
        const rectWidth = textWidth + 12
        const rectHeight = 28
        const r = 2
        ctx.beginPath()
        ctx.moveTo(rectX + r, rectY)
        ctx.arcTo(rectX + rectWidth, rectY, rectX + rectWidth, rectY + rectHeight, r)
        ctx.arcTo(rectX + rectWidth, rectY + rectHeight, rectX, rectY + rectHeight, r)
        ctx.arcTo(rectX, rectY + rectHeight, rectX, rectY, r)
        ctx.arcTo(rectX, rectY, rectX + rectWidth, rectY, r)
        ctx.closePath()
        ctx.fill()

        ctx.fillStyle = '#fff'
        ctx.textBaseline = 'middle'
        ctx.textAlign = 'center'
        ctx.fillText(text, startX, startY - 5 - 14)
      }
    })
  }

  onSettingModalClose = () => {
    this.setState({
      showSettingModal: false
    })
  }

  onTechnicalIndicatorModalClose = () => {
    this.setState({
      showTechnicalIndicatorModal: false
    })
  }

  onPeriodClick = (period) => {
    const { currentPeriod } = this.state
    if (currentPeriod !== period) {
      this.setState({
        currentPeriod: period
      })
    }
  }

  onTechnicalIndicatorClick = () => {
    this.setState({
      showTechnicalIndicatorModal: !this.state.showTechnicalIndicatorModal,
      showSettingModal: false
    })
  }

  onSettingClick = () => {
    this.setState({
      showTechnicalIndicatorModal: false,
      showSettingModal: !this.state.showSettingModal
    })
  }

  onLanguageClick = () => {
    this.setState({
      language: this.state.language === 'zh-CN' ? 'en-US' : 'zh-CN'
    })
  }

  onThemeClick = () => {
    this.setState({
      theme: this.state.theme === 'dark' ? 'light' : 'dark'
    })
  }

  onScreenshotClick = () => {
    const { theme } = this.state
    if (this.kLineChart) {
      const url = this.kLineChart.getConvertPictureUrl(
        true, true,
        'png', theme === 'dark' ? '#1E2126' : '#FFFFFF'
      )
      const a = document.createElement('a')
      a.download = 'screenShot'
      a.href = url
      document.body.appendChild(a)
      a.click()
      a.remove()
    }
  }

  onLoadHistoryData = (symbol, exchange, period, time, onHistoryCallback, onErrorCallback, isFirst) => {
    asyncData(period, !isFirst).then(
      kLineDataList => {
        onHistoryCallback(kLineDataList, kLineDataList.length === 500)
      }
    )
  }

  subscribe = (symbol, exchange, period, onRealtimeCallback) => {
    this.updateData(period, onRealtimeCallback)
  }

  updateData (period, onRealtimeCallback) {
    const timeout = translatePeriodToMillisecond(period)
    this.updateDataTimer = setTimeout(() => {
      const hisKLineDataList = this.kLineChart.getDataList()
      const lastKLineData = hisKLineDataList[hisKLineDataList.length - 1]
      const timestamp = lastKLineData.timestamp + timeout
      const kLineData = {
        timestamp,
        open: lastKLineData.close,
        low: lastKLineData.close,
        high: lastKLineData.close,
        close: lastKLineData.close,
        volume: 0,
        turnover: 0
      }
      onRealtimeCallback(kLineData)
      this.updateData(period, onRealtimeCallback)
    }, timeout)
    clearTimeout(this.realUpdateDataTimer)
    this.updateRealData(onRealtimeCallback)
  }

  updateRealData (onRealtimeCallback) {
    this.realUpdateDataTimer = setTimeout(() => {
      const hisKLineDataList = this.kLineChart.getDataList()
      const lastKLineData = hisKLineDataList[hisKLineDataList.length - 1]
      const kLineData = { ...lastKLineData }
      const price = Math.floor((Math.random() - 0.5) * 10) / 10 + kLineData.close
      const volume = Math.floor(Math.random() * 5)
      kLineData.close = price
      kLineData.high = Math.max(kLineData.high, price)
      kLineData.low = Math.min(kLineData.low, price)
      kLineData.volume = kLineData.volume + volume
      kLineData.turnover = kLineData.turnover + volume * price
      onRealtimeCallback(kLineData)
      this.updateRealData(onRealtimeCallback)
    }, 800)
  }

  unsubscribe = () => {
    clearTimeout(this.realUpdateDataTimer)
    clearTimeout(this.updateDataTimer)
  }

  componentWillUnmount () {
    this.unsubscribe()
  }

  render () {
    const {
      theme,
      language,
      currentPeriod,
      showTechnicalIndicatorModal,
      showSettingModal
    } = this.state
    return (
      <div
        className={`k-line-chart-${theme}`}>
        <TopBar
          theme={theme}
          language={language}
          period={currentPeriod}
          onPeriodClick={this.onPeriodClick}
          onTechnicalIndicatorClick={this.onTechnicalIndicatorClick}
          onSettingClick={this.onSettingClick}
          onLanguageClick={this.onLanguageClick}
          onThemeClick={this.onThemeClick}
          onScreenshotClick={this.onScreenshotClick}
        />
        <StockChart
          className="k-line-chart-container"
          theme={theme}
          status="ok"
          language={language}
          symbol="xxx"
          exchange="xxx"
          chartType={currentPeriod === 'Line' ? 'line' : 'candle'}
          period={currentPeriod}
          showTechnicalIndicatorModal={showTechnicalIndicatorModal}
          showSettingModal={showSettingModal}
          defaultMainTechnicalIndicators={['MA']}
          defaultSubTechnicalIndicators={['VOL', 'MACD']}
          onChartReady={this.onChartReady}
          onSettingModalClose={this.onSettingModalClose}
          onTechnicalIndicatorModalClose={this.onTechnicalIndicatorModalClose}
          onLoadHistoryData={this.onLoadHistoryData}
          subscribe={this.subscribe}
          unsubscribe={this.unsubscribe}
        />
      </div>
    )
  }
}
