import 'intl'
import 'intl/locale-data/jsonp/en'
import React from 'react'
import ReactDOM from 'react-dom'

import intl, { ZH_CN, EN_US } from './component/intl'
import zhCN from './locals/zh-CN.json'
import en from './locals/en.json'

import KLineChart from './pages/klinechart/KLineChart'
// import KLineChart from './pages/klinechart/SimpleKLineChart'
// import DepthChartSample from './DepthChartSample'
import * as serviceWorker from './serviceWorker'

import './index.less'

import 'overlayscrollbars/css/OverlayScrollbars.css'

intl.load(ZH_CN, zhCN)
intl.load(EN_US, en)

ReactDOM.render(
  <KLineChart />,
  document.getElementById('root')
)

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.register()
