import React, { PureComponent, Fragment } from 'react'
import PropTypes from 'prop-types'

import { OverlayScrollbarsComponent } from 'overlayscrollbars-react'

import Modal from '../../../../component/modal'
import Checkbox from '../../../../component/checkbox'
import NumberInput from '../../../../component/numberinput'
import Button from '../../../../component/button'

import intl from '../../../../component/intl'

import { allMainTechnicalIndicators, allTechnicalIndicators } from './config'

export default class TechnicalIndicatorModal extends PureComponent {
  static propTypes = {
    language: PropTypes.string,
    mainTechnicalIndicators: PropTypes.array,
    subTechnicalIndicators: PropTypes.array,
    defaultTechnicalIndicatorParams: PropTypes.object,
    technicalIndicatorParams: PropTypes.object,
    onMainTechnicalIndicatorChange: PropTypes.func,
    onSubTechnicalIndicatorChange: PropTypes.func,
    onTechnicalIndicatorParamsChange: PropTypes.func,
    onTechnicalIndicatorParamsConfirmClick: PropTypes.func,
    onClose: PropTypes.func
  }

  constructor (props) {
    super(props)
    this.state = {
      currentSelectedTechnicalIndicator: 'MA'
    }
  }

  onMainTechnicalIndicatorCheckedChange = (name) => (checked) => {
    const { onMainTechnicalIndicatorChange } = this.props
    let controlType
    if (checked) {
      controlType = 'add'
    } else {
      controlType = 'delete'
    }
    onMainTechnicalIndicatorChange(name, controlType)
  }

  onSubTechnicalIndicatorCheckedChange = (name) => (checked) => {
    const { onSubTechnicalIndicatorChange } = this.props
    let controlType
    if (checked) {
      controlType = 'add'
    } else {
      controlType = 'delete'
    }
    onSubTechnicalIndicatorChange(name, controlType)
  }

  technicalIndicatorParamChange = (paramIndex) => (value) => {
    const { technicalIndicatorParams, onTechnicalIndicatorParamsChange } = this.props
    const { currentSelectedTechnicalIndicator } = this.state
    const params = [...technicalIndicatorParams[currentSelectedTechnicalIndicator]]
    params[paramIndex] = value
    onTechnicalIndicatorParamsChange && onTechnicalIndicatorParamsChange(currentSelectedTechnicalIndicator, params)
  }

  onConfirmClick = () => {
    const { onTechnicalIndicatorParamsConfirmClick, onClose } = this.props
    const { currentSelectedTechnicalIndicator } = this.state
    onTechnicalIndicatorParamsConfirmClick && onTechnicalIndicatorParamsConfirmClick(currentSelectedTechnicalIndicator)
    onClose && onClose()
  }

  resetDefaultClick = () => {
    const { defaultTechnicalIndicatorParams, onTechnicalIndicatorParamsChange } = this.props
    const { currentSelectedTechnicalIndicator } = this.state
    const params = [...defaultTechnicalIndicatorParams[currentSelectedTechnicalIndicator]]
    onTechnicalIndicatorParamsChange && onTechnicalIndicatorParamsChange(currentSelectedTechnicalIndicator, params)
  }

  technicalIndicatorSelected = (name) => () => {
    this.setState({
      currentSelectedTechnicalIndicator: name
    })
  }

  renderMainTechnicalIndicator () {
    const { currentSelectedTechnicalIndicator } = this.state
    const { mainTechnicalIndicators, language } = this.props
    return (
      <Fragment>
        <li
          className="modal-content-list-row-title">
          {intl.get('main_technical_indicator', language)}
        </li>
        {
          allMainTechnicalIndicators.map(name => {
            const checked = mainTechnicalIndicators.indexOf(name) > -1
            return (
              <li
                key={name}
                className={
                  `${currentSelectedTechnicalIndicator === name && 'modal-content-list-row-selected'} ${checked && 'modal-content-list-row-checked'}`
                }
                onClick={this.technicalIndicatorSelected(name)}>
                <Checkbox
                  checked={checked}
                  labelClassName="modal-content-list-row-label"
                  label={intl.get(name.toLowerCase(), language)}
                  trigger="icon"
                  onChange={this.onMainTechnicalIndicatorCheckedChange(name)}/>
              </li>
            )
          })
        }
      </Fragment>
    )
  }

  renderSubTechnicalIndicator () {
    const { currentSelectedTechnicalIndicator } = this.state
    const { subTechnicalIndicators, language } = this.props
    return (
      <Fragment>
        <li
          className="modal-content-list-row-title">
          {intl.get('sub_technical_indicator', language)}
        </li>
        {
          allTechnicalIndicators.map(name => {
            const checked = subTechnicalIndicators.indexOf(name) > -1
            return (
              <li
                key={name}
                className={
                  `${currentSelectedTechnicalIndicator === name && 'modal-content-list-row-selected'} ${checked && 'modal-content-list-row-checked'}`
                }
                onClick={this.technicalIndicatorSelected(name)}>
                <Checkbox
                  labelClassName="modal-content-list-row-label"
                  checked={checked}
                  label={intl.get(name.toLowerCase(), language)}
                  trigger="icon"
                  onChange={this.onSubTechnicalIndicatorCheckedChange(name)}/>
              </li>
            )
          })
        }
      </Fragment>
    )
  }

  renderTechnicalIndicatorParams () {
    const { language, technicalIndicatorParams } = this.props
    const { currentSelectedTechnicalIndicator } = this.state
    const params = technicalIndicatorParams[currentSelectedTechnicalIndicator] || []
    return params.map((param, index) => {
      return (
        <li
          key={`key_${index}`}>
          <span
            className="modal-content-technical-indicator-row-title">
            {intl.get('calc_param', language)}{index + 1}
          </span>
          <NumberInput
            className="modal-content-technical-indicator-row-input"
            value={param}
            min={1}
            onChange={this.technicalIndicatorParamChange(index)}/>
        </li>
      )
    })
  }

  render () {
    const { language, onClose } = this.props
    return (
      <Modal
        draggable
        title={intl.get('indicator', language)}
        innerContentStyle={{ padding: 0 }}
        onClose={onClose}>
        <div
          className="modal-content">
          <OverlayScrollbarsComponent
            style={{ height: 448 }}>
            <ul
              className="modal-content-list-container modal-content-left-container">
              {this.renderMainTechnicalIndicator()}
              {this.renderSubTechnicalIndicator()}
            </ul>
          </OverlayScrollbarsComponent>
          <ul
            className="modal-content-list-container modal-content-technical-indicator-container">
            <li
              className="modal-content-list-row-title">
              {intl.get('params_setting', language)}
            </li>
            {this.renderTechnicalIndicatorParams()}
          </ul>
          <div
            className="modal-content-button-container">
            <Button
              className="modal-content-reset-button"
              onClick={this.resetDefaultClick}>
              {intl.get('reset', language)}
            </Button>
            <Button
              className="modal-content-confirm-button"
              onClick={this.onConfirmClick}>
              {intl.get('confirm', language)}
            </Button>
          </div>
        </div>
      </Modal>
    )
  }
}
