import moment from 'moment'

let timer
export default function asyncData (period, isMore) {
  clearTimeout(timer)
  return new Promise((resolve, reject) => {
    timer = setTimeout(() => {
      let dataList = []
      switch (period) {
        case 'Line': {
          dataList = [...(require('./1m.json'))]
          break
        }
        case '1m': {
          dataList = [...(require('./1m.json'))]
          break
        }
        case '5m': {
          dataList = [...(require('./5m.json'))]
          break
        }
        case '15m': {
          dataList = [...(require('./15m.json'))]
          break
        }
        case '30m': {
          dataList = [...(require('./30m.json'))]
          break
        }
        case '1H': {
          dataList = [...(require('./1h.json'))]
          break
        }
        case '2H': {
          dataList = [...(require('./2h.json'))]
          break
        }
        case '4H': {
          dataList = [...(require('./4h.json'))]
          break
        }
        case '6H': {
          dataList = [...(require('./6h.json'))]
          break
        }
        case '12H': {
          dataList = [...(require('./12h.json'))]
          break
        }
        case '1D': {
          dataList = [...(require('./1d.json'))]
          break
        }
        case '1W': {
          dataList = [...(require('./week.json'))]
          break
        }
        case '1M': {
          dataList = [...(require('./month.json'))]
          break
        }
      }
      let shouldTransferDataList = []
      if (isMore) {
        if (dataList.length > 500) {
          shouldTransferDataList = dataList.slice(0, dataList.length - 500)
        }
      } else {
        if (dataList.length > 500) {
          shouldTransferDataList = dataList.slice(dataList.length - 500)
        } else {
          shouldTransferDataList = dataList
        }
      }
      const kLineDataList = shouldTransferDataList.map(data => {
        const kLineData = {
          timestamp: moment(data[0]).valueOf(),
          open: +data[1],
          high: +data[2],
          low: +data[3],
          close: +data[4],
          volume: Math.ceil(+data[5]),
          mark: data[6] || ''
        }
        kLineData.turnover = (kLineData.open + kLineData.low + kLineData.high + kLineData.close) / 4 * kLineData.volume
        return kLineData
      })
      resolve(kLineDataList)
    }, 1000)
  })
}
